html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  margin-block-end: 50vh;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  font-size: 2.625rem;
  font-size: clamp(2.5rem, 1.77rem + 3.64vw, 4.5rem);
}

h2 {
  font-size: 2.25rem;
  font-size: clamp(2.25rem, 1.91rem + 1.45vw, 3rem);
}

h3 {
  font-size: 1.5rem;
}

p {
  margin-top: 0;
  font-size: 1rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --main-bg: hsla(0, 0%, 100%, 1);
  --text-dark: hsla(233, 14%, 25%, 1);
  --text-light: hsla(0, 0%, 100%, 1);
  --header-bg: hsla(232, 58%, 55%, 1);
  --hero-bg: hsla(232, 58%, 55%, 1);
  --button-primary-bg: hsla(180, 100%, 42%, 1);
  --button-primary-text: hsla(0, 0%, 100%, 1);
  --button-secondary-bg: transparent;
  --button-secondary-border: hsla(0, 0%, 100%, 1);
  --button-secondary-text: hsla(0, 0%, 100%, 1);
  --button-cta-bg: hsla(0, 0%, 100%, 1);
  --button-cta-text: hsla(0, 0%, 100%, 1);
  --fullwidth-bg: hsla(320, 85%, 41%, 1);
  --cta-desktop: linear-gradient(
    90deg,
    hsla(233, 14%, 25%, 1) 0%,
    hsla(232, 58%, 55%, 1) 56.34%,
    hsla(180, 100%, 42%, 1) 112.47%
  );
  --cta-mobile: linear-gradient(
    90deg,
    hsla(233, 14%, 25%, 1) 0%,
    hsla(232, 58%, 55%, 1) 76.28%,
    hsla(180, 100%, 42%, 1) 152.28%
  );
}

.wrapper {
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
}

.topnav {
  background-color: var(--header-bg);
}
.topnav__wrapper {
  display: grid;
  grid-template-columns: repeat(2, auto);
  justify-content: space-between;
  align-items: center;
  padding-block: 12px;
}
@media (min-width: 56.25em) {
  .topnav__wrapper {
    padding-block: 20px;
  }
}
.topnav__homelink:hover {
  opacity: 0.9;
}
.topnav__logo {
  display: block;
  width: 4.375rem;
  height: auto;
}
@media (min-width: 56.25em) {
  .topnav__logo {
    width: 8rem;
  }
}
.topnav__links {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 20px;
  align-items: center;
  margin: 0;
  padding: 0;
}
@media (min-width: 56.25em) {
  .topnav__links {
    gap: 40px;
  }
}
.topnav__item {
  list-style-type: none;
}
.topnav__link {
  font-size: 0.875rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  color: var(--text-light);
  text-transform: uppercase;
}
@media (min-width: 56.25em) {
  .topnav__link {
    font-size: 1rem;
  }
}
.topnav__link:hover {
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-decoration-color: var(--text-light);
  text-underline-offset: 4px;
}